import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { LoginServiceProvider } from './../../providers/login-service/login-service';
import { FormBuilder } from '@angular/forms';
import { RequestOptions } from '@angular/http';
import { CookieService } from 'angular2-cookie/core';
import { TabsPage } from '../tabs/tabs';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  public loginForm;
  loading: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public formBuilder: FormBuilder,
    public nav: NavController,
    private loginService: LoginServiceProvider,
    private cookieService: CookieService,
    private requestOptions: RequestOptions,
  ) {

    this.loginForm = formBuilder.group({
      email: [''],
      senha: ['']
    });

  }
  

  private loginUser(): void {
    if (this.loginForm.valid) {
      this.loginService.login(this.loginForm.value).subscribe(
        response => this.loginSuccess(response)
      );
    } else {
      this.loading.present();
    }
  }

  private loginSuccess(response: any) {
    this.cookieService.removeAll();
    this.cookieService.put("accessToken", response.access_token);
    this.requestOptions.headers.set('Authorization', "Bearer" + response.access_token);
    this.loginService.getUsuarioAtual(response.access_token).subscribe(
      response => this.redirectPage(response)
    );
  }

  public redirectPage(response: any) {
    this.cookieService.putObject("usuarioAtual", response);
    this.navCtrl.setRoot(TabsPage);
  }

  redirectUser(response) {
    this.cookieService.removeAll();
    this.cookieService.put("accessToken", response.access_token);
    this.requestOptions.headers.set('Authorization', "Bearer" + response.access_token);
  }

}



